.App {
  display: flex;
  position: relative;
  overflow: hidden;
  max-width: 650px;
  text-align: center;
  background-color: #fff;
  margin: 0 auto;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.App__container {
  width: 650px;
  height: 500px;
  max-width: 650px;
  margin: 0 auto;
  user-select: none;
  position: relative;
  z-index: 0;
  background-image: url("./images/bg.png");
}

.grid {
  padding-top: 6px;
}

.row {
  display: flex;
  justify-content: space-around;
}

.message {
  width: 249px;
  margin: 8px auto;
  padding: 13px 18px;
  background-color: #fff;
  box-shadow: 0 1px 7px 0 rgba(0, 0, 0, 0.3);
  border-radius: 32px;
  font-family: "Proxima Nova";
  font-size: 18px;
  line-height: 20px;
  font-weight: bold;
}

.cell {
  position: relative;
  width: 60px;
  height: 60px;
  /* border: 1px solid rgba(0, 0, 0, 0.1); */
}

.cell img {
  display: block;
  max-width: none;
  position: absolute;
  z-index: 50;
}
.hole {
  position: absolute;
  bottom: 0;
  width: 60px;
  height: 12px;
  background-color: #000;
  border-radius: 50%;
}
.target {
  position: absolute;
  z-index: 100;
  top: -47px;
  left: 8px;
  width: 43px;
  height: 56px;
  background-image: url("./images/sechin.png");
  background-size: cover;
  transform: scale(0);
  opacity: 0;
  transform-origin: bottom;
  animation: target 0.65s;
}
.footer {
  width: 100%;
  position: absolute;
  bottom: 18px;
}

.gameOver {
  position: relative;
  width: 100%;
  height: 100%;
}

.bottomBlock {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 195px;
  background-color: #e9e9e9;
  border-top: 3px solid #000;
}

.container {
  position: absolute;
  bottom: 195px;
  right: 20%;
}

.house {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  max-width: none;
}

.button {
  font-size: 15px;
  font-weight: 700;
  line-height: 1;
  position: relative;
  z-index: 0;
  display: inline-block;
  min-width: 120px;
  height: 32px;
  padding: 0 15px;
  cursor: pointer;
  word-spacing: -.5px;
  color: #fff;
  border: 1px solid rgba(0,0,0,.15);
  border-radius: 4px;
  outline: none;
  box-shadow: inset 0 -1px 0 0 rgba(0,0,0,.2);
  white-space: nowrap;
}

.button::after {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  transition: opacity .1s ease-in-out;
  opacity: 0;
  background-color: #000;
}

.car {
  position: absolute;
  width: 84px;
  bottom: 0;
  left: -600px;
  max-width: none;
  animation: car 3s;
}

.preloaded {
  width: 1px;
  height: 1px;
  opacity: 0;
  position: absolute;
  bottom: -1px;
  right: -1px;
}

@media (max-width: 649px) {
  .root .target {
    animation: target 0.45s !important;
  }
}

@keyframes target {
  0% {
    transform: scale(0);
    opacity: 0;
  }

  35% {
    transform: scale(1);
    opacity: 1;
  }
  65% {
    transform: scale(1);
    opacity: 1;
  }

  100% {
    transform: scale(0);
    opacity: 0;
  }
}

@keyframes car {
  from {
    left: -600px;
  }

  to {
    left: -15px;
  }
}
